<template>
  <div class="index-box">
    <el-row class="top" v-if="userInfo">
      <el-col :span="8">
        <div class="grid-content">
          <div class="content" style="margin-left: 0">
            <div class="title">Withdraw Wallet</div>
            <div class="text">{{Number(userInfo.reward/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content">
          <div class="content">
            <div class="title">Total Package</div>
            <div class="text">{{Number((userInfo.investAmount + userInfo.reinvestAmount)/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content">
          <div class="content">
            <div class="title">Total Bonus</div>
            <div class="text">{{Number((userInfo.topReward + userInfo.bottomReward + userInfo.introReward)/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content">
          <div class="content" style="margin-right: 0">
            <div class="title">Upline Bonus</div>
            <div class="text">{{Number(userInfo.topReward/10**6).toFixed(4)}}1000trx</div>
          </div>
        </div>
      </el-col>
       <el-col :span="8">
        <div class="grid-content">
          <div class="content" style="margin-right: 0">
            <div class="title">Downline Bonus</div>
            <div class="text">{{Number(userInfo.bottomReward/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content">
          <div class="content" style="margin-right: 0">
            <div class="title">Sponsor Bonus</div>
            <div class="text">{{Number(userInfo.introReward/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>
      <el-col :span="8">
        <div class="grid-content">
          <div class="content" style="margin-right: 0">
            <div class="title">Withdraw Amount</div>
            <div class="text">{{Number(userInfo.haveWithdraw/10**6).toFixed(4)}}trx</div>
          </div>
        </div>
      </el-col>

    </el-row>
   <el-row class="top" v-if="userInfo">
      <el-col :span="12">
        <div class="grid-content">
          <div class="content" style="margin-left: 0">
            <div class="title">Username</div>
            <div class="text">{{userInfo.address}}</div>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content">
          <div class="content">
            <div class="title">Joining Date</div>
            <div class="text">{{dateFormat(
              "yyyy-MM-dd hh:mm:ss",
              new Date(userInfo.createTime * 1000)
            )}}</div>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content">
          <div class="content">
            <div class="title">My Sponsor</div>
            <div class="text">{{userInfo.intro}}</div>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content">
          <div class="content" style="margin-right: 0">
            <div class="title">Total Referrals</div>
            <div class="text">{{userInfo.childrenNum}}</div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { useI18n } from "vue-i18n"
import {
  getUserInfo
} from "@/api";
export default {
  components: {},
  setup() {
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      userInfo: null
    });
    onMounted(() => {
      getUserInfo().then((res) => {
        data.userInfo = res.data
      })
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

<style lang="scss" scoped>
.top {
  margin-bottom: 30px;
}
.grid-content {
  padding: 15px 10px;
  .content {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    .title {
      font-size: 18px;
      color: #666;
      margin-bottom: 5px;
    }
    .text {
      font-size: 26px;
      font-weight: bold;
      color: #333;
    }
  }
}
.card-header {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
}
</style>
