import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../views/index";

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index
  },
  {
    path: "/user",
    name: "User",
    component: () => import(/* webpackChunkName: "Block" */ "../views/user")
  },
  {
    path: "/user/detail/:address",
    name: "UserDetail",
    component: () => import(/* webpackChunkName: "Block" */ "../views/user/detail")
  },
  {
    path: "/history",
    name: "History",
    component: () => import(/* webpackChunkName: "Block" */ "../views/history")
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
